@import "./pages/profile";
@import "colors";

.sidebar {
  .nav-dropdown.open {
    .nav-link {
      color: #000;
      &:hover {
        color: #fff;
      }
      &:active,
      &.active {
        color: #fff;
      }
    }
  }
  .nav-link {
    color: #000;
    background-color: #fff;
    &.active {
      color: #fff;
      background-color: $PrimaryBgColor !important;
    }
    &:hover {
      background-color: $PrimaryBgColor !important;
      transition: 0.3s background-color ease-in-out;
    }
    &.active {
      .nav-icon {
        color: #fff;
      }
    }
  }
}
a,
.active {
  color: $PrimaryBgColor;
  &:hover,
  &:active {
    color: $PrimaryBgActive;
  }
}

.btn-bg {
  background-color: $PrimaryBgColor !important;
  border-color: $PrimaryBgColor !important;
  &:hover,
  &:active,
  &:focus {
    background-color: $PrimaryBgActive !important;
    border-color: $PrimaryBgActive !important;
    box-shadow: 0 0 0 0.2rem rgba(247, 68, 132, 0.24) !important;
  }
}

.border {
  border-color: $PrimaryBgColor !important;
  &:hover,
  &:active,
  &:focus {
    box-shadow: 0 0 0 0.2rem rgba(247, 68, 132, 0.24) !important;
    outline: none;
  }
}

.form-control {
  &:focus,
  &:active {
    border-color: $PrimaryBgColor;
    box-shadow: 0 0 0 0.2rem rgba(247, 68, 132, 0.24);
  }
}
